import React, { Component } from 'react'
import Header from '@/components/Header/Header'

class Document extends Component {
	render() {
		return (
			<div>
				<Header></Header>
				<div className="doc-container">
					<h1></h1>
					<h3></h3>
					<p></p>
					<p></p>
					<p></p>
					<p></p>
					<p></p>
					<p></p>
					<p></p>
				</div>
			</div>
		)
	}
}

export { Document as default }


